<template>
  <Card class="card">
    <Breadcrumb>
      <BreadcrumbItem>市场管理</BreadcrumbItem>
      <BreadcrumbItem>货物供需</BreadcrumbItem>
    </Breadcrumb>
    <div class="box-container">
      <Row class="row">
        <Button type="primary" @click="addPower">添加电厂</Button>
        <Modal
          v-model="showModal"
          :title="titleInfo"
          @on-ok="ok"
          @on-cancel="cancel"
          width='430'
        >
        <Row class="row">
            <Col span="4">电厂名称</Col>
            <Col span="10">
               <Input placeholder="请输入电厂名称" style="width:255px" v-model="powerPlantName"/>
            </Col>
        </Row>
        <Row class="row">
            <Col span="4">经度</Col>
            <Col span="7">
               <Input placeholder="请输入经度" style="width:90px" v-model="lng"/>
            </Col>
            <Col span="3">纬度</Col>
            <Col span="4">
               <Input placeholder="请输入纬度" style="width:90px" v-model="lat"/>
            </Col>
        </Row>
        </Modal>
      </Row>
       <Collapse v-model="value1" accordion style="margin-top:20px;width:80%" >
        <Panel v-for="(list,index) in powerInfo" :key='index'  :name='String(index)'>
           <span>电厂:{{list.powerPlantName}}</span>
           <span class="loaction">坐标:{{list.lng}} ,{{list.lat}}</span>
           <Button type="primary" class="update" @click.stop.prevent="update(list.powerPlantCode,list.powerPlantName,list.lat,list.lng)" size='small'>修改</Button>
           <Button type="warning" class="delete" @click.stop.prevent="deleteFun(list.powerPlantCode)" size='small'>删除</Button>
          <Row class="row " slot="content">
            <Col span="1">日耗</Col>
            <Col span="10"> <Input placeholder="请输入日耗" style="width:240px"  v-model="list.dailyConsumption"/> 元/吨</Col>
            <Col span="1">库存</Col>
            <Col span="10">
              <Input placeholder="请输入库存" style="width:240px"  v-model="list.reserve"/>
               <Button type="primary" size='small'  style="margin-left:10px;" @click.stop.prevent="save(list.powerPlantCode,index,'save')">保存</Button>
            </Col>
           </Row>
           <Row slot="content">
             <table class="table table-row">
                 <tr>
                     <td>#</td>
                     <td>日耗</td>
                     <td>库存</td>
                     <td>操作</td>
                 </tr>
                 <tr v-for="(key,i) in list.powerPlantDailyInfo" :key="i">
                     <td>{{i+1}}</td>
                     <td v-if="!key.isEditor">{{key.dailyConsumption}}</td>
                     <td v-else > <Input style="width:100px " placeholder="请输入日耗" v-model="key.dailyConsumption" /></td>
                     <td v-if="!key.isEditor">{{key.reserve}}</td>
                     <td v-else><Input style="width:100px " placeholder="请输入库存"  v-model="key.reserve"  /></td>
                     <td v-if="!key.isEditor">
                       <Button type="primary" size='small' @click.stop.prevent="handleEditor(index,i)">修改</Button>
                       <Button type="warning" size='small' @click.stop.prevent="deleteFn(key.dailyCode)">删除</Button>
                     </td>
                     <td v-else>  <Button type="primary" size='small' @click.stop.prevent="save(list.powerPlantCode,index,i)">保存</Button></td>
                 </tr>
             </table>
          </Row>
        </Panel>
      </Collapse>

    </div>
  </Card>
</template>
<script>
export default {
  data() {
    return {
      showModal: false,
      titleInfo:'添加电厂信息',
      powerInfo:[],
      powerPlantName:'',
      position:'',
      status:1, // 修改电厂2 添加 1
      id:0, // 电厂id
      value1:'1',
      lat:'',
      lng:""
    };
  },
  methods: {
    update(id,name,lat,lng){
      this.titleInfo = '修改电厂信息'
      this.showModal = true;
      this.powerPlantName = name
      this.lat = lat
      this.lng = lng
      this.status = 2
      this.id = id
      // this.addPowerPlantInfo(id,2)
    },
    async deleteFun(id){
      let data  = {
         code:id
      }
     try{
        let res = await  this.$http.deletePowerPlantInfo(data)
        if(!res.data.status){
          this.$Notice.success({
            desc:'删除成功.'
          })
          this.getPowerPlantInfo()
        }
        else{
            this.$Notice.error({
            desc:res.data.msg
          })
        }


        }
        catch(err){
          console.log(err)
        }
      },
    addPower() {
      this.titleInfo = '添加电厂信息'
      this.showModal = true;
    },
    // 添加电厂
    ok() {
      if(this.status ==1){

        this.addPowerPlantInfo('',1)
        }
      else{
        this.addPowerPlantInfo(this.id,2)

      }

    },
    cancel() {
      this.lat = ''
      this.lng = ''
      this.powerPlantName =''
    },
    addPlus(){
      let obj = {
        lineStart:'',
        lineEnd:'',
        linePrice:'',
      }
      this.lineArr.push(obj)
    },
    minusPlus(index){
        this.lineArr.splice(index,1)
    },
    // 获取电厂详情
    async getPowerPlantInfo(){
      try {

        let  res = await this.$http.getPowerPlantInfo()
        this.powerInfo = []
        var result = res.data.result
        if(0==res.data.status && res.data.result instanceof Array){
            for(let i in result){
              for(let j in result[i]['powerPlantDailyInfo']){
                result[i]['powerPlantDailyInfo'][j].isEditor = false
              }
            }
            result.map((item,index)=>{
                  this.powerInfo.push(
                      Object.assign({},item,{
                        value:index+1+"",
                        dailyConsumption:"",
                        powerPlantDailyInfoId:'',
                        reserve:'',
                        isCollapse:false
                      })
                  )
            })
          // console.log(  this.powerInfo ,'==')
        }
        else{

        }

      }
       catch(err){
         console.log(err)
       }
    },
  /**
   * 判断 str 字符串中是否含有字符串 subStr
   * @param {} str 原字符串
   * @param {} subStr 要查找的字符串
   * @param {} isIgnoreCase 是否忽略大小写
   * @return {Boolean}
   */
   contains(str, subStr, isIgnoreCase) {
      if (isIgnoreCase) {
        // 忽略大小写
        str = str.toLowerCase();
        subStr = subStr.toLowerCase();
      }

      var startChar = subStr.substring(0,1);
      var strLen = subStr.length;

      for (var j=0; j<str.length-strLen+1; j++) {
        if (str.charAt(j) == startChar) {
          /* 如果匹配起始字符,开始查找 */
          if (str.substring(j, j+strLen) == subStr) {
            /*如果从j开始的字符与 str 匹配 */
            return true;
          }
        }
      }
      return false;
    },

    // 添加 /修改电厂
    // id 新增 0 修改对应的 id
   async addPowerPlantInfo(id,params){

        let data = {
          powerPlantName:this.powerPlantName,
          position:this.lng + ',' + this.lat,
          powerPlantCode:id,
        }
        try {
          let res = await this.$http.addPowerPlantInfo(data)
            if(0==res.data.status ){
             let msg = params == 1?'添加成功':'修改成功'
                this.$Notice.success({
                  desc:msg
                })
             if(params==2){
               this.status = 1
               this.id = 0
             }
              this.powerPlantName = ''
              this.position = ''
              this.lat = ''
              this.lng = ''
              this.getPowerPlantInfo()
            }
            else{
               this.$Notice.error({
                desc:res.data.msg
              })
            }
            if(params ==1){
               this.powerPlantName = ''
               this.position = ''
               this.lat = ''
               this.lng = ''
            }
        }
        catch (err){

        }

    },
    handleEditor(outIndex,index){
        this.powerInfo[outIndex]['powerPlantDailyInfo'][index]['isEditor'] = true
    },
    // parms == save 说明是保存 else 编辑
   async save(id,index,params){
     let data ;
     if(params == 'save'){
       let {dailyConsumption,reserve} = this.powerInfo[index]
       let saveData = {
           dailyConsumption,
           reserve,
           powerPlantCode:id,
           powerPlantDailyCode:''
         }
         data = saveData
     }
     else{

       let {dailyConsumption,reserve,dailyCode} = this.powerInfo[index]['powerPlantDailyInfo'][params]
         let editData = {
           dailyConsumption,
           reserve,
           powerPlantCode:id,
           powerPlantDailyCode:dailyCode
         }
         data = editData
     }
        try{
          let res = await this.$http.addPowerPlantDailyInfo(data)
          if(0==res.data.status){
              this.$Notice.success({
                desc:'保存成功.'
              })
              if(params == 'save'){
                this.powerInfo[index].dailyConsumption = ''
                this.powerInfo[index].reserve = ''
              }

              this.getPowerPlantInfo()
          }
          else{
             this.$Notice.error({
                desc:res.data.msg
              })
          }
        }
        catch(err){

        }


    },
    // 删除电厂日耗详情
   async deleteFn(id){
      let data = {
        code:id
      }
      try {
          let res = await this.$http.deletePowerPlantDailyInfo(data)
          if(!res.data.status){
            this.$Notice.success({
              desc:'删除成功.'
            })
            this.getPowerPlantInfo()
          }
          else{
              this.$Notice.error({
              desc:res.data.msg
            })
          }
      }

      catch(err){

      }
    },
  },
  created(){
    this.getPowerPlantInfo()
  }
};
</script>
<style lang="scss" scoped>
.table-row{
  margin: 20px 0px;
}
.row{
  line-height: 45px;
}
.loaction{
  position: absolute;
  left: 46%;
}
.update{
  position: absolute;
  right:120px;
  top: 6px;
  z-index: 900000;

}
.delete{
  top: 6px;
  position: absolute;
  right: 20px;
  z-index: 888000;
}
.table, tr ,td{
  border: 1px solid #e4e4ee;
  border-collapse: collapse;
  padding: 8px 30px;
  text-align: center;
}
</style>



